.main-access-header-div{
    background: var(--material-theme-sys-light-on-background, #1F1F1F);
    padding: 5px 0px;
}
.gov-text{
    color: #EBEBEB;
font-family: var(--roboto-font);
font-size: 14px;
font-style: normal;
font-weight: 500;
line-height: normal;
}
.skip-text{
    color: #fff;
font-family: Roboto;
font-size: 12px;
font-style: normal;
font-weight: 500;
line-height: normal;
margin: 0px;
cursor: pointer;
transition: all 0.3s ease-in-out;
}
.skip-text:hover{
    color: #fff;
    text-decoration: underline;
}
.screen-reader-div{
  cursor: pointer;
}
.dark-mode{
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.inc-div{
    padding: 8px;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
}
.inc-div:hover{
    border-radius: 4px;
    background: rgba(255, 255, 255, 0.15);
}

.accessibility-header {
    text-align: center;
    padding: 20px;
  }
  
  .language-selector {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 20px;
  }
  
  .language-selector button {
    padding: 10px 15px;
    border: 1px solid #ccc;
    background: #f1f1f1;
    cursor: povar(--roboto-font);
    transition: 0.3s;
  }
  
  .language-selector button:hover {
    background: #ddd;
  }
  

  .text-content {
    text-align: center;
    transition: font-size 0.3s;
  
    h1, h2, h3, h4, h5, h6, p, span {
      transition: font-size 0.3s;
    }
  }

  .level-popup {
    position: fixed;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 9999;
    background: #000000a1;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    border-radius: 12px;
    padding: 30px;
    transition: opacity 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: fadeIn 0.5s ease;
  }
  
  .popup-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  /* Icon and Label */
  .text-icon-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  
    img {
      width: 100px;          // Adjust icon size
      height: 100px;
    }
  
    .bigger-text-label {
      font-size: 18px;
      font-weight: bold;
      color: var(--primary-white);
      margin-top: 10px;
    }
  }
  
  /* Level Indicator */
  .level-indicator {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    margin-top: 10px;
  }
  
  .level-indicator span {
    width: 30px;
    height: 6px;
    background: var(--brand-color);    // Inactive color
    border-radius: 4px;
    transition: background 0.3s ease;
  }
  
  .level-indicator span.active {
    background: var(--primary-white);     // Active color
  }
  
  /* Animation for smooth appearance */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateX(-60%);
    }
    to {
      opacity: 1;
      transform: translateX(-50%);
    }
  }
  .mirrored {
    transform: scaleX(-1);
  }

      .social-icons {
      display: flex;
      gap: 10px;

      @media (max-width: 600px) {
        justify-content: start;
      }

      a {
        width: 30px;
        height: 30px;
        background: #ffffff;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #000;
        font-size: 14px;
        transition: 0.2s;
        text-decoration: none;
        opacity: 0.7;

        &:hover {
          background: #ddd;
          opacity: 1;
        }
      }
    }

  @media screen and (max-width: 768px){
    .skip-text{
      display: none;
    }
    .screen-reader-div{
      display: none !important;
    }
    .font-inc-dec{
      display: none !important;
    }
  }